import { Component } from '@angular/core';
import { FirebaseConfigurationService } from '../../../services/firebase-configuration.service';
import { HttpClient } from '@angular/common/http';
import { saveAs } from 'file-saver';

@Component({
  selector: 'app-configuration',
  templateUrl: './configuration.component.html',
  styleUrl: './configuration.component.scss'
})
export class ConfigurationComponent {
  publicKey: string = '';
  secretKey: string = '';
  newPublicKey: string = '';
  newSecretKey: string = '';
  formattedPricePaymentIntent: string = '';
  newPricePaymentIntent: number = 0;
  priceId: string = '';
  newPriceId: string = '';
  loading: {}= {
    publicKey: false,
    secretKey: false,
    priceId: false,
    pricePaymentIntent: false
  }

  languages: { [key: string]: string } = {};
  newLanguageCode: string = '';
  newLanguageName: string = '';
  fileToUpload: File | null = null;
  fileLoading: boolean = false;

  isLoading: boolean = true;

  constructor(private configService: FirebaseConfigurationService, private http: HttpClient) {}

  ngOnInit(): void {
    this.loadConfig();
    this.loadLanguages();
  }

  loadConfig(): void {
    this.configService.getPublicKey().subscribe(key => {
      this.publicKey = key;
      this.newPublicKey = key;
      this.checkLoading();
    });

    this.configService.getSecretKey().subscribe(key => {
      this.secretKey = key;
      this.newSecretKey = key;
      this.checkLoading();
    });

    this.configService.getPriceKey().subscribe(id => {
      this.priceId = id;
      this.newPriceId = id;
      this.checkLoading();
    });

    this.configService.getPricePaymentIntent().subscribe(price => {
      this.newPricePaymentIntent = price;
      this.updateFormattedPrice();
      this.checkLoading();
    })
  }

  onPriceInput(event: any) {
    const value = event.target.value.replace(/[^0-9.,]/g, ''); // Rimuovi caratteri non numerici
    const numericValue = parseFloat(value.replace(',', '.'));
    this.newPricePaymentIntent = Math.round(numericValue * 100); // Converti in centesimi
    this.updateFormattedPrice();
  }

  updateFormattedPrice() {
    this.formattedPricePaymentIntent = (this.newPricePaymentIntent / 100).toLocaleString('it-IT', {
      style: 'currency',
      currency: 'EUR'
    });
  }

  checkLoading(): void {
    if (this.publicKey && this.secretKey && this.languages && this.priceId && this.formattedPricePaymentIntent) {
      this.isLoading = false;
    }
  }

  updatePublicKey(): void {
    this.loading['publicKey'] = true;
    this.configService.updatePublicKey(this.newPublicKey).then(() => {
      this.publicKey = this.newPublicKey;
      setTimeout(() => {
        this.loading['publicKey'] = false;
      }, 500);
    }).catch(error => {
      console.error('Error updating public key:', error);
    });
  }

  updateSecretKey(): void {
    this.loading['secretKey'] = true;
    this.configService.updateSecretKey(this.newSecretKey).then(() => {
      this.secretKey = this.newSecretKey;
      setTimeout(() => {
        this.loading['secretKey'] = false;
      }, 500);
    }).catch(error => {
      console.error('Error updating secret key:', error);
    });
  }

  updatePriceId(): void{
    this.loading['priceId'] = true;
    this.configService.updatePriceKey(this.newPriceId).then(() =>{
      this.priceId = this.newPriceId;
      setTimeout(() =>{
        this.loading['priceId'] = false;
      }, 500);
    }).catch(error => {
      console.error('Error updatin public id:', error);
    });
  }

  updatePricePaymentIntent() {
    this.loading['pricePaymentIntent'] = true;
    this.configService.updatePricePaymentIntent(this.newPricePaymentIntent).then(() => {
      this.updateFormattedPrice();
      setTimeout(() => {
        this.loading['pricePaymentIntent'] = false;
      }, 500);
    }).catch(error => {
      console.error('Error updatin price payment intent:', error);
    });
  }

  ngOnChanges() {
    this.loadConfig();
  }

  loadLanguages(): void {
    this.configService.getLanguages().subscribe(languages => {
      this.languages = languages;
      this.checkLoading();
    });
  }

  addLanguage(): void {
    if (this.newLanguageCode && this.newLanguageName) {
      this.configService.addLanguage(this.newLanguageCode, this.newLanguageName).then(() => {
        this.newLanguageCode = '';
        this.newLanguageName = '';
        this.loadLanguages();
      }).catch(error => {
        console.error('Error adding language:', error);
      });
    }
  }

  deleteLanguage(code: string): void {
    this.configService.deleteLanguage(code).then(() => {
      this.loadLanguages();
    }).catch(error => {
      console.error('Error deleting language:', error);
    });
  }

  downloadEsJson(): void {
    const url = '/assets/i18n/es.json';
    this.http.get(url, { responseType: 'text' })
      .subscribe(data => {
        const blob = new Blob([data], { type: 'application/json' });
        saveAs(blob, 'es.json');
      }, error => {
        console.error('Error downloading es.json:', error);
      });
  }

  getLanguageKeys(): string[] {
    return this.languages ? Object.keys(this.languages) : [];
  }
}
